import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import StarIcon from '../assets/landing_star.svg'

const StyledRating = styled.div``

const RatingStar = styled(StarIcon)`
  width: 10px;
  margin-right: 2px;

  .star__inner {
    fill: ${props => props.color || props.theme.colors.primary};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(10)};
    margin-right: ${desktopVw(2)};
  }
`

const LandingRating = ({ className, color }) => {
  return (
    <StyledRating className={className}>
      {[...Array(5)].map((_, i) => (
        <RatingStar color={color} key={i} className='rating__star' />
      ))}
    </StyledRating>
  )
}

export default LandingRating
