import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw, mobileVw } from '../styles/utils'
import theme from '../styles/theme'

import ScaledImage from './ScaledImage'
import Button from './Button'
import { getLinkHref, getLinkPath, imageSources } from '../utils'
import useDetectDevices from '../hooks/useDetectDevices'

const iosBar = 95

const StyledHero = styled.section`
  position: relative;
  box-sizing: border-box;
  height: calc(
    100vh - ${props => props.theme.announcementBar.mobile.height + iosBar}px
  );
  min-height: 542px;
  margin: -${props => props.theme.header.mobile.height}px 0 0;
  padding: ${props => props.theme.spacings.mobile}px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: calc(
      100vh - ${props => desktopVw(props.theme.announcementBar.desktop.height)}
    );
    padding: ${desktopVw(30)} ${desktopVw(40)};
    margin: -${props => desktopVw(props.theme.header.desktop.height)} 0 0;
  }
`

const Body = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
  }
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    justify-content: space-between;
  }
`

const TitleWrapper = styled.div`
  text-align: center;
  justify-self: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(150)} 0 0 0;
  }
`

const Title = styled.div`
  font-size: ${props =>
    props.fontSizeDesktop ? `${props.fontSizeDesktop}px` : '30px'};
  line-height: 40px;
  white-space: pre-wrap;
  text-transform: uppercase;
  margin-bottom: 20px;

  h1,
  h2,
  p {
    color: ${props =>
      props.fontColorTitle
        ? props.fontColorTitle
        : props.theme.colors.secondary};
  }

  i {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: 45px;
    text-transform: none;
    font-style: normal;
    font-weight: 600;
  }

  u {
    text-decoration: none;
    vertical-align: super;
    > i {
      font-weight: normal;
      font-size: ${desktopVw(35)};
    }
  }

  @media (max-width: ${props => props.theme.breakpoints.mobile}px) {
    font-size: 25px;
    line-height: 1.05;

    i {
      font-size: 35px;
    }

    u {
      > i {
        font-size: 20px;
      }
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.mobile}px) {
    font-size: ${props => (props.fontSize ? props.fontSize : '35')}px;
    line-height: 1.05;

    i {
      font-size: ${props =>
        props.fontSizeItalic ? props.fontSizeItalic : '50'}px;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
    font-size: ${props =>
      props.fontSizeDesktop ? desktopVw(props.fontSizeDesktop) : desktopVw(70)};
    line-height: 0.85;

    h1,
    h2,
    p {
      color: ${props =>
        props.fontColorTitleDesktop
          ? props.fontColorTitleDesktop
          : props.theme.colors.secondary};
    }

    i {
      font-size: ${props =>
        props.fontSizeItalicDesktop
          ? desktopVw(props.fontSizeItalicDesktop)
          : desktopVw(94)};
    }
  }
`

const TitleDesktop = styled(Title)`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`
const ButtonContainer = styled.div`
  display: flex;
  flex-direction: column;
`

const StyledButton = styled(Button)`
  display: inline-block;
  padding: 17px 35px;
  border: 1px solid
    ${props =>
      props.fontColorTitle
        ? props.fontColorTitle
        : props.theme.colors.secondary};
  color: ${props =>
    props.fontColorTitle ? props.fontColorTitle : props.theme.colors.secondary};
  margin-bottom: 25px;
  font-size: 18px;

  @media (min-width: ${theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(18)};
    margin-bottom: ${desktopVw(60)};
    border: 1px solid
      ${props =>
        props.fontColorTitleDesktop
          ? props.fontColorTitleDesktop
          : props.theme.colors.secondary};
    color: ${props =>
      props.fontColorTitleDesktop
        ? props.fontColorTitleDesktop
        : props.theme.colors.secondary};
    width: ${desktopVw(275)};
    padding: ${desktopVw(28)} 0;
    text-transform: uppercase;
  }
`

const AnotherStyledButton = styled(StyledButton)`
  display: none;

  @media (min-width: ${theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const BotTitle = styled.h1`
  font-size: 0;
  width: 1px;
  height: 1px;
  display: inline-block;
  overflow: hidden;
  position: absolute !important;
  border: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  clip: rect(1px, 1px, 1px, 1px);
`

/*
 do not remove this, since the container has flex and justify-content: space-between
 this is needed to center the text
*/
const EmptyDiv = styled.div`
  display: block;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: ${props => (props.onlyMobile ? 'none' : 'block')};
  }
`

const Hero = ({
  className,
  title,
  titleDesktop,
  imageMobile,
  imageDesktop,
  button,
  anotherButton,
  card,
  titleSize,
  titleSizeDesktop,
  fontSizeItalic,
  fontSizeItalicDesktop,
  fontColorTitle,
  fontColorTitleDesktop,
  disableBotTitle,
}) => {
  const { devices } = useDetectDevices()

  function renderButtonContainer() {
    return (
      <ButtonContainer className='hero__button-container'>
        {button && (
          <StyledButton
            className='hero__button'
            to={button.url ? undefined : getLinkPath(button)}
            href={getLinkHref(button)}
            target='_blank'
            trackParams={{
              trackGA: true,
              category: 'navigation',
              action: 'click_hero_card',
              label: button.text,
              value: button.url ? getLinkHref(button) : getLinkPath(button),
            }}
            fontColorTitle={fontColorTitle}
            fontColorTitleDesktop={fontColorTitleDesktop}
          >
            {button.text}
          </StyledButton>
        )}
        {anotherButton && (
          <AnotherStyledButton
            className='hero__another_button'
            to={getLinkPath(anotherButton)}
            trackParams={{
              trackGA: true,
              category: 'navigation',
              action: 'click_hero_card',
              label: anotherButton.text,
              value: getLinkPath(anotherButton),
            }}
            fontColorTitle={fontColorTitle}
            fontColorTitleDesktop={fontColorTitleDesktop}
          >
            {anotherButton.text}
          </AnotherStyledButton>
        )}
      </ButtonContainer>
    )
  }

  return (
    <StyledHero className={className}>
      <ScaledImage
        className='hero__scaled_image'
        backgroundColor={theme.colors.green}
        fluid={imageSources(imageMobile, imageDesktop)}
        style={{
          position: 'absolute',
          left: '0',
          right: '0',
          bottom: '0',
          top: '0',
          zIndex: '-1',
        }}
        alt={imageDesktop.description || imageDesktop.title}
        loading='eager'
        fadeIn={false}
      />
      <Body className='hero__body'>
        <Content className='hero__content'>
          {devices.desktop && <EmptyDiv />}
          <TitleWrapper className='hero__title_wrapper'>
            {!disableBotTitle && (
              <BotTitle>
                Quince{' '}
                {/** This is needed so that bots can identify the name. Not a visible element on the site. */}
              </BotTitle>
            )}
            <Title
              className='hero__title'
              fontSize={titleSize}
              fontSizeDesktop={titleSizeDesktop}
              fontSizeItalic={fontSizeItalic}
              fontSizeItalicDesktop={fontSizeItalicDesktop}
              fontColorTitle={fontColorTitle}
            >
              {documentToReactComponents(title.json)}
            </Title>
            <TitleDesktop
              className='hero__title'
              fontSize={titleSize}
              fontSizeDesktop={titleSizeDesktop}
              fontSizeItalic={fontSizeItalic}
              fontSizeItalicDesktop={fontSizeItalicDesktop}
              fontColorTitleDesktop={fontColorTitleDesktop}
            >
              {documentToReactComponents(titleDesktop.json)}
            </TitleDesktop>
          </TitleWrapper>
          {renderButtonContainer()}
        </Content>
      </Body>
    </StyledHero>
  )
}

export default Hero
